<template>
  <div>
    <div>
      <div>
        <h1>Calculadora de Colas - Investigación Operativa II</h1>
      </div>
      <div>
        <p><b>Nombres: </b>Gary Limbert Apaza Mamani</p>
      </div>
      <div class="card">
        <div class="card-header">
          <h3 class="fw-bold text-center">
            COLA {{ setCanal }} POBLACIÓN {{ setPoblacion }}
          </h3>
        </div>
        <div class="container m-auto card-body row">
          <div
            class="col-12 d-flex align-items-center justify-content-between fs-5 text-center mt-2"
          >
            (
            <span>M</span>
            /
            <span>M</span>
            /
            <span>S</span>
            :
            <span>FIFO</span>
            /
            <span>N</span>
            /
            <span>N</span>
            )
          </div>
          <div class="col-12 d-flex align-items-center fs-2 text-center">
            (
            <input
              type="number"
              class="form-control"
              min="1"
              v-model="lambda"
              placeholder="λ"
              @change="changeM()"
            />
            /
            <input
              type="number"
              class="form-control"
              min="1"
              v-model="mu"
              placeholder="µ"
              @change="changeM()"
            />
            /
            <input type="number" class="form-control" min="1" v-model="S" />
            :
            <input v-model="tipo" class="form-control" type="text" readonly />
            /
            <input
              type="number"
              class="form-control"
              min="0"
              v-model="N1"
              placeholder="∞"
              @change="changeN()"
            />
            /
            <input
              type="number"
              class="form-control"
              min="0"
              v-model="N2"
              placeholder="∞"
              @change="changeN()"
            />
            )
          </div>
          <button class="btn btn-success my-2 w-25 m-auto" @click="resolver()">
            Resolver
          </button>

          <hr />

          <div>
            <h4 class="fw-bold">Resultados</h4>
            <p><b>δ = </b>{{ delta }}</p>
            <h6 class="fw-bold">Probabilidad de encontrar el sistema vacio</h6>
            <p>
              <b>Po = </b>{{ Po }}
              <span v-if="result">
                => <b>{{ (Po * 100).toFixed(2) }}%</b> de probabilidad de
                encontrar el sistema vacio.</span
              >
            </p>
            <h6 class="fw-bold">Numero esperado de clientes en la cola</h6>
            <p>
              <b>Lq = </b>{{ Lq }}
              <span v-if="result">
                => <b>{{ Math.round(Lq) }}</b> clientes en la cola</span
              >
            </p>
            <h6 class="fw-bold">Numero esperado de clientes en el sistema</h6>
            <p>
              <b>L = </b>{{ L }}
              <span v-if="result">
                => <b>{{ Math.round(L) }}</b> clientes en el sistema</span
              >
            </p>
            <h6 class="fw-bold">Tiempo esperado de clientes en la cola</h6>
            <p><b>Wq = </b>{{ Wq }}</p>
            <h6 class="fw-bold">Tiempo esperado de clientes en el sistema</h6>
            <p><b>W = </b>{{ W }}</p>
            <h6 class="fw-bold">
              Probabilidad de encontrar clientes en el sistema
            </h6>
            <b>N: </b
            ><input
              type="number"
              min="1"
              v-model="n"
              class="form-control w-25 d-inline"
              @change="calcularN()"
            />
            <p>
              <b>Pn = </b>{{ Pn }}
              <span v-if="nVer">
                => <b>{{ (Pn * 100).toFixed(2) }}%</b> de probabilidad de
                encontrar el sistema vacio.</span
              >
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeView",
  data() {
    return {
      cola: "",
      poblacion: "",
      lambda: null,
      mu: null,
      S: null,
      tipo: "FIFO",
      N1: null,
      N2: null,
      delta: null,
      deltaS: null,
      Po: null,
      n: null,
      Pn: null,
      nVer: false,
      Lq: null,
      L: null,
      Wq: null,
      W: null,
      result: false,
    };
  },
  computed: {
    setCanal() {
      if (this.S == 1) {
        return "MONOCANAL";
      } else {
        return "MULTICANAL";
      }
    },
    setPoblacion() {
      if (this.N1 && this.N2) {
        return "FINITA";
      } else {
        return "INFINITA";
      }
    },
  },
  methods: {
    changeM() {
      if (this.lambda <= 0) {
        this.lambda = null;
      }
      if (this.mu <= 0) {
        this.mu = null;
      }
    },
    changeN() {
      if (this.N1 <= 0) {
        this.N1 = null;
      }
      if (this.N2 <= 0) {
        this.N2 = null;
      }
    },
    resolver() {
      this.lambda == null
        ? this.$swal("λ vacio")
        : this.mu == null
        ? this.$swal("µ vacio")
        : this.S == null
        ? this.$swal("S vacio")
        : (this.delta = this.lambda / this.mu);

      if (this.setCanal == "MONOCANAL" && this.setPoblacion == "INFINITA") {
        this.Po = 1 - this.delta;
        this.Lq =
          Math.pow(this.lambda, 2) / (this.mu * (this.mu - this.lambda));
        this.L = this.lambda / (this.mu - this.lambda);
        this.Wq = this.lambda / (this.mu * (this.mu - this.lambda));
        this.W = 1 / (this.mu - this.lambda);
        this.result = true;
      }
      if (this.setCanal == "MULTICANAL" && this.setPoblacion == "INFINITA") {
        this.Po =
          this.sumatoriaMulticanalInfinita(0, this.S) + ((1 / this.factorial(this.S)) *
            Math.pow(this.delta, this.S) *
            ((this.S * this.mu) / ((this.S * this.mu) - this.lambda)));

        this.result = true
      }
    },
    calcularN() {
      this.nVer = true;
      this.Pn = Math.pow(this.delta, this.n) * this.Po;
    },
    factorial(n) {
      let res = 1;
      if (n == 0) {
        res = 1;
      } else {
        for (let i = 1; i <= n; i++) {
          res = res * i;
        }
      }

      return res;
    },
    sumatoriaMulticanalInfinita(i, k) {
      let res = 0;
      for (let n = i; n <= k - 1; n++) {
        res = res + (1 / this.factorial(n)) * Math.pow(this.delta, n);
      }
      return res;
    },
  },
};
</script>